import React, { useState } from 'react'
import Papa from "papaparse";
import { Icon, Label, Menu, Table, Button, Segment, Container, Form, TextArea, Dropdown, Header } from 'semantic-ui-react';
import axios from 'axios';
import { Link } from 'react-router-dom'
import { useNavigate } from 'react-router-dom'
import Navbar from '../Navbar/Navbar';
import Footer from '../Footer/Footer';


const Upload = () => {
    const navigate = useNavigate();
    const [parsedData, setParsedData] = useState([]);
    const [tableRows, setTableRows] = useState([]);
    const [values, setValues] = useState([]);
    const [jsonData, setJsonData] = useState([]);



    const changeHandler = (event) => {
       
        Papa.parse(event.target.files[0], {
            header: true,
            skipEmptyLines: true,
            complete: function (results) {
                const rowsArray = [];
                const valuesArray = [];
                console.log(results.data);
                setJsonData(results.data);
                
                results.data.map((d) => {
                    rowsArray.push(Object.keys(d));
                    valuesArray.push(Object.values(d));
                });

                setParsedData(results.data);
                setTableRows(rowsArray[0]);
                setValues(valuesArray);
            },
        });
    };

    const hasDuplicate = (arrayObj, colName) => {
        var hash = Object.create(null);
        return arrayObj.some((arr) => {
            return arr[colName] && (hash[arr[colName]] || !(hash[arr[colName]] = true));
        });
    };

    const sendDataToAPI = () => {
        if (parsedData.length > 0) {
            var isDuplicate = hasDuplicate(parsedData, "learnerId");
            if (!isDuplicate) {
                axios.post('/api/v1/bulkupload', parsedData)
                    .then((response) => {
                        if (response.data.status == 'Failed') {
                            alert(response.data.Message);
                        }
                        else if (response.data.status == 'OK') {
                            alert(response.data.Message);
                            navigate('/home');
                        }
                    })
                    .catch(function (error) {
                        console.log(error.toJSON());
                    });
            }
            else {
                alert('Upload failed, LearnerId duplication found in uploaded file!');
            }
        }
        else {
            alert('No data found!');
        }
    }
    return (
        <div>
            <div>
                <Navbar/>
                
                <div className="container">
                    
                    <div className="place-holder">

                        <Header as='h3'>Upload Csv File</Header>
                        <Segment>
                            <Form style={{border:"none"}}>
                                <Form.Field>
                                    <label>Select CSV File</label>
                                    <input
                                        type="file"
                                        name="file"
                                        onChange={changeHandler}
                                        accept=".csv"
                                        style={{ display: "block", margin: "10px auto" }} />
                                </Form.Field>

                                <Table celled>
                                    <Table.Header >
                                        <Table.Row >
                                            {tableRows.map((rows, index) => {
                                                return (
                                                    <Table.HeaderCell style={{backgroundColor:"#581845", color:"#ffffff"}}>{rows}</Table.HeaderCell>)
                                            })}
                                        </Table.Row>

                                    </Table.Header>

                                    <Table.Body>
                                        {values.map((value, index) => {
                                            return (
                                                <Table.Row>
                                                    {value.map((val, i) => {
                                                        return <Table.Cell>{val}</Table.Cell>
                                                    })}

                                                </Table.Row>
                                            )
                                        })}
                                    </Table.Body>
                                </Table>

                                <Button size='mini' color='grey' type='submit' onClick={sendDataToAPI} style={{backgroundColor:"#FFC300", color:"#ffffff", fontSize:15, borderColor:"#FF0000"}} >Submit</Button>

                                <Button size='mini'  style={{backgroundColor:"#FF0000", color:"#ffffff", fontSize:15, borderColor:"#FFC300"}}>
                                    <Link to='/home' >Cancel</Link>
                                </Button>
                            </Form>
                        </Segment>
                    </div>
                </div>
            </div>
            <Footer/>
            </div>
    )
}

export default Upload